.bed {
    width: 150px;
    position: absolute;
    border-top: 10px solid var(--theme-diff);
    border-bottom: 5px solid transparent;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    transform: translate(15px, 165px);

}

/* hotend case */
.hotend {
    width: 45px;
    height: 45px;
    background-color: var(--theme-mid);
    position: absolute;
    /* transform: translate(78px, -10px); */
    animation: move linear 8000ms;
    animation-name: move;
    animation-timing-function: linear;
    animation-duration: 8000ms;
    animation-direction: reverse;
    offset-path: path("M 102 -158 V -140.4 H 116.4 Q 121.2 -143.6 116.4 -138.8 H 82.8 Q 78 -134 82.8 -129.2 H 154.8 Q 159.6 -124.4 154.8 -119.6 H 44.4 Q 39.6 -114.8 44.4 -110 H 154.8 Q 159.6 -105.2 154.8 -100.4 H 44.4 Q 39.6 -95.6 44.4 -90.8 H 154.8 Q 159.6 -86 154.8 -81.2 H 42");
    offset-rotate: 0deg;
    transform: translate(-6.5px,-7px)
}

@keyframes move {
  100% {
    offset-distance: 100%;
  }
}

/* hotend fan */
.hotend:before {
    content: '';
    width: 30px;
    height: 30px;
    position: absolute;
    background-color: var(--theme-mid);
    border: 2.5px dotted var(--light);
    filter: contrast(55%);
    border-radius: 50%;
    transform: translate(5px, 5px);
}

/* hotend nozzle */
.hotend:after {
    content: '';
    width: 30px;
    height: 20px;
    position: absolute;
    background-color: var(--theme-mid);
    filter: contrast(25%);
    -webkit-clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    transform: translate(7.5px, 45px);
}

path {
  stroke-dasharray: 634.277;
  /* stroke-dashoffset: 634.277; */
  animation: draw linear 8000ms forwards;
  animation-direction: forwards;
}

@keyframes draw {
  from {
      stroke-dashoffset: 634.277;
  }
  to {
      stroke-dashoffset: 1268.554;
  }
}

.App {
  height: 100%;
  display: flex;

  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;


  background-color: var(--theme-page-background);
  color: var(--theme-page-text);

}

.container {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: space-between;
  padding: 2em 2em 20em 2em;
  margin: 2em;
  background: var(--cucumber-water);
  color: var(--theme-button-text);
}

.toggle-theme {
  position: absolute;
  top: 0;
  right: 0;

  padding: 0.75em;
  margin: 1.5em;
  border-radius: .5rem;
  text-align: center;
  background-color: var(--theme-button-color);
  color: var(--theme-button-text);
  font-size: .75em;
  border: 0 solid transparent;
  z-index: 1000;
}

.toggle-theme:hover {
  background-color: var(--theme-hover-color);
}

.toggle-theme :focus {
  outline: none;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {

  h1 {
    font-size: calc(2em + 2vw) !important;
    font-family: 'Proxima Nova';
  }

  h3 {
    font-size: calc(1em + 1vw) !important;
    font-family: 'Proxima Nova';
  }

}

/* Small devices (portrait tablets and large phones, 600 and up) */
@media only screen and (min-width: 600px) {}

